@import url('https://fonts.googleapis.com/css?family=Lato|Quicksand');

body {
  margin: 0;
  padding: 0;
  font-family: 'Quicksand', sans-serif;
  color: #28587B;
  background: #ECE9E6;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #FFFFFF, #ECE9E6);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#FFFFFF), to(#ECE9E6));
  background: -webkit-linear-gradient(top, #FFFFFF, #ECE9E6);
  background: -o-linear-gradient(top, #FFFFFF, #ECE9E6);
  background: linear-gradient(to bottom, #FFFFFF, #ECE9E6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -ms-flex-direction: column;
      flex-direction: column;
}

main {
  -ms-flex: 1 0 auto;
      flex: 1 0 auto;
}



